
.title {
    font-size: 24px;
    color: orange;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .products-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .product-item {
    width: 300px;
    margin: 20px;
    padding: 20px;
    border: 1px solid orange;
    border-radius: 5px;
    text-align: center;
    background-color: white;
  }
  
  .product-image {
    width: 200px;
    height: 200px;
    object-fit: cover;
    margin-bottom: 10px;
  }
  
  .product-title {
    font-size: 18px;
    color: orange;
    margin-bottom: 5px;
  }
  
  .product-price {
    font-size: 16px;
    color: black;
    margin-bottom: 5px;
  }
  
  .product-discount {
    font-size: 14px;
    color: darkorange;
    margin-bottom: 5px;
  }
  
  .add-button,
  .view-button {
    margin-top: 10px;
    padding: 8px 16px;
    border: none;
    border-radius: 5px;
    background-color: orange;
    color: white;
    cursor: pointer;
  }
  
  .add-button:hover,
  .view-button:hover {
    background-color: darkorange;
  }
 
  .image {
    width: 200px;
    height: 200px;
    object-fit: cover;
  }
  .product {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .content {
    flex: 1 1 300px;
    margin: 20px;
    padding: 20px;
    border: 2px solid orange;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 100px;
  }
  .heading{
    margin-top: 240%;
    font-size: 50px;
  }
